h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.page {
  size: 32px;
  color: #292929;
  scroll-behavior: smooth;
  font-family: Space Grotesk, sans-serif;
  line-height: 32px;
}

.page__body {
  margin: 0;
  overflow: auto;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  width: 100%;
  transition: opacity 1s, visibility 1s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

@media (width >= 768px) {
  .page__menu {
    --coluns: 1;
    grid-template-columns: repeat(var(--coluns), 1fr);
    grid-template-rows: auto auto 1fr;
    column-gap: 20px;
    margin-inline: auto;
    display: grid;
  }
}

@media (width >= 768px) and (width >= 768px) {
  .page__menu {
    --coluns: 6;
    column-gap: 30px;
  }
}

@media (width >= 768px) and (width >= 1280px) {
  .page__menu {
    --coluns: 12;
  }
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  height: 100%;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

* {
  transition: transform 1s;
}

.header {
  --coluns: 1;
